import { Component } from 'react'
import "./MyStory.css"

class MyStory extends Component {
    constructor(props) {
        super(props)
        this.showbtn = this.showbtn.bind(this)
    }
    
    showbtn() {
        document.querySelector(".chevron").classList.toggle("rotate-chevron")
    }
    
    render() {
        return (
        <div className="text-white mt-4">
            <p>
              <button onClick={this.showbtn} className="showbtn btn text-white w-100 btn-outline-primary border-primary btn-sm" data-bs-toggle="collapse" data-bs-target="#collapseMyStory" aria-expanded="false" aria-controls="collapseMyStory">
                <i className="h5 chevron text-success fa fa-angle-double-down"></i> Read My Story ðŸ“ƒ 
              </button>
            </p>
            <div className="collapse" id="collapseMyStory">
                <div className="text-white card card-body bg-transparent border-primary">
                    <h4>My history with the internet</h4>
                    <ul>
                        <li>
                            <h5>When did it all start?</h5>
                            <ul>
                                <p>
                                It all started in 2012 when I tried to create a blog on Blogspot. At first, it worked well. But, after only a few days, I was limited by the features available. This motivated me to find a different solution, that's when I started learning web development. My goal is to take the knowledge and skills I have acquired in a positive direction. More than just enjoying myself, I want to help others.
                                </p>
                            </ul>
                        </li>
                    </ul>
                </div>
            </div>
        </div>
        );
    }
}

export default MyStory